import './ChatWidget.css'

import { ChevronRight, Send, StopCircle } from 'lucide-react'
import React, { useCallback, useEffect, useMemo, useRef, useState } from 'react'
import ReactMarkdown, { Options } from 'react-markdown'
import remarkGfm from 'remark-gfm'

import { useChat,UseChatConfig } from '../hooks/useChat'
import { UIWidget } from '../schemas'
import { cn } from '../utils/cn'
import { RegisteredWidgetDefinition } from '../widgets/default-widgets'
import { SpeechToTextButton } from './SpeechToTextButton'
import { VoicePlayerButton } from './VoicePlayerButton'

const FormattedMarkdown: React.FC<Options> = ({ children }) => {
  const MarkdownComponent = ReactMarkdown as React.ComponentType<Options>;
  return (
    <MarkdownComponent remarkPlugins={[remarkGfm]}>
      {children!.replace(/\n\n/gi, "\n\n&nbsp;\n\n")}
    </MarkdownComponent>
  );
}

export interface ChatWidgetStyles {
  primaryColor?: string
  primaryTextColor?: string
  backgroundColor?: string
  borderColor?: string
  headerTextColor?: string
  userBubbleTextColor?: string
  assistantBubbleBackground?: string
  assistantBubbleTextColor?: string
  fontFamily?: string
  borderRadius?: string
  inputBackground?: string
  inputTextColor?: string
  inputPlaceholderColor?: string
  thoughtsBorderColor?: string
}

export interface ChatWidgetVoiceOptions {
  url: string
  enabled?: boolean
  method?: string
  headers?: Record<string, string>
  payload?: Record<string, unknown> | ((text: string) => Record<string, unknown>)
  autoplayLatest?: boolean
}

export interface ChatWidgetSpeechToTextOptions {
  enabled?: boolean
  silenceTimeout?: number
  language?: string
  onError?: (message: string) => void
  finalDelay?: number
}

export interface ChatWidgetConfig extends UseChatConfig {
  /** Title for the Chat window */
  title?: string;
  /** Introductory message displayed at the start of the chat for brand new threads */
  introMessage?: string;
  /** Placeholder text for the input box */
  inputPlaceholder?: string;

  className?: string;
  styles?: ChatWidgetStyles;
  voice?: ChatWidgetVoiceOptions;
  speechToText?: ChatWidgetSpeechToTextOptions;
  widgets?: RegisteredWidgetDefinition[];
}

const classNames = {
  container: 'chat-widget',
  header: 'chat-widget__header',
  headerTitle: 'chat-widget__title',
  messagesContainer: 'chat-widget__messages',
  messageWrapper: 'chat-widget__message',
  messageRow: 'chat-widget__message-row',
  userMessageRow: 'chat-widget__message-row--user',
  assistantMessageRow: 'chat-widget__message-row--assistant',
  messageBubble: 'chat-widget__bubble',
  userMessage: 'chat-widget__bubble--user',
  assistantMessage: 'chat-widget__bubble--assistant',
  thoughtsContainer: 'chat-widget__thoughts',
  thoughtsSummary: 'chat-widget__thoughts-summary',
  thoughtsIconContainer: 'chat-widget__thoughts-icon-container',
  thoughtsIcon: 'chat-widget__thoughts-icon',
  thoughtsContent: 'chat-widget__thoughts-content',
  thoughtsText: 'chat-widget__thoughts-text',
  loadingContainer: 'chat-widget__loading',
  loadingBubble: 'chat-widget__loading-bubble',
  loadingDots: 'chat-widget__loading-dots',
  loadingDot: 'chat-widget__loading-dot',
  inputContainer: 'chat-widget__input',
  inputForm: 'chat-widget__form',
  textarea: 'chat-widget__textarea',
  toolbar: 'chat-widget__toolbar',
  submitButton: 'chat-widget__submit',
  sendIcon: 'chat-widget__send-icon',
  voiceControl: 'chat-widget__voice',
  micButton: 'chat-widget__mic-button',
}

const defaultTheme: Required<ChatWidgetStyles> = {
  primaryColor: '#3b82f6',
  primaryTextColor: '#ffffff',
  backgroundColor: '#ffffff',
  borderColor: '#e5e7eb',
  headerTextColor: '#ffffff',
  userBubbleTextColor: '#ffffff',
  assistantBubbleBackground: '#f3f4f6',
  assistantBubbleTextColor: '#111827',
  fontFamily: `'Inter', system-ui, -apple-system, BlinkMacSystemFont, sans-serif`,
  borderRadius: '0.75rem',
  inputBackground: '#ffffff',
  inputTextColor: '#111827',
  inputPlaceholderColor: '#6b7280',
  thoughtsBorderColor: '#d1d5db',
}

const mixWithBlack = (hex: string, ratio = 0.15) => {
  const normalized = hex.replace('#', '')
  if (!/^[0-9a-fA-F]{6}$/.test(normalized)) return hex
  const r = parseInt(normalized.slice(0, 2), 16)
  const g = parseInt(normalized.slice(2, 4), 16)
  const b = parseInt(normalized.slice(4, 6), 16)

  const mix = (value: number) => Math.round(value * (1 - ratio))

  return `#${mix(r).toString(16).padStart(2, '0')}${mix(g)
    .toString(16)
    .padStart(2, '0')}${mix(b).toString(16).padStart(2, '0')}`
}

export function ChatWidget({
  api,
  threadId,
  agentId,
  agentExecutionId,
  dataContext,
  onError,
  onAuthError,
  onFinish,
  onMessage,
  title,
  introMessage,
  inputPlaceholder,
  className,
  styles: styleProps,
  voice,
  speechToText,
  widgets,
}: ChatWidgetConfig) {
  const theme = useMemo(
    () => ({ ...defaultTheme, ...(styleProps || {}) }),
    [styleProps]
  )

  const themeVariables = useMemo(
    () => ({
      '--chat-primary': theme.primaryColor,
      '--chat-primary-text': theme.primaryTextColor,
      '--chat-primary-hover': mixWithBlack(theme.primaryColor, 0.2),
      '--chat-background': theme.backgroundColor,
      '--chat-border': theme.borderColor,
      '--chat-header-text': theme.headerTextColor,
      '--chat-user-text': theme.userBubbleTextColor,
      '--chat-assistant-bg': theme.assistantBubbleBackground,
      '--chat-assistant-text': theme.assistantBubbleTextColor,
      '--chat-font-family': theme.fontFamily,
      '--chat-radius': theme.borderRadius,
      '--chat-input-bg': theme.inputBackground,
      '--chat-input-text': theme.inputTextColor,
      '--chat-input-placeholder': theme.inputPlaceholderColor,
      '--chat-thought-border': theme.thoughtsBorderColor,
    }) as React.CSSProperties,
    [theme]
  )

  const [input, setInput] = useState('')
  const [isMicActive, setIsMicActive] = useState(false)
  const [isSpeechPending, setIsSpeechPending] = useState(false)
  const messagesEndRef = useRef<HTMLDivElement>(null)
  const textareaRef = useRef<HTMLTextAreaElement>(null)

  const {
    messages,
    status,
    sendMessage,
    stop,
    setMessages,
  } = useChat({
    api,
    threadId,
    agentId: agentId ?? undefined,
    agentExecutionId: agentExecutionId ?? undefined,
    dataContext,
    onError,
    onAuthError,
    onFinish,
    onMessage,
  });

  const voiceConfig = useMemo(() => {
    if (!voice || voice.enabled === false || !voice.url) {
      return null
    }

    const payloadConfig = voice.payload
    const buildPayload = (text: string) => {
      if (!payloadConfig) {
        return { message: text }
      }

      if (typeof payloadConfig === 'function') {
        return payloadConfig(text)
      }

      return { message: text, ...payloadConfig }
    }

    return {
      url: voice.url,
      method: voice.method ?? 'POST',
      autoplayLatest: voice.autoplayLatest ?? true,
      headers: {
        ...(voice.headers ?? {}),
      },
      buildPayload,
    }
  }, [voice])

  // Add intro message on mount
  useEffect(() => {
    if (introMessage && messages.length === 0) {
      setMessages([{
        id: 'welcome-message',
        role: 'assistant' as const,
        parts: [{ type: 'text' as const, text: introMessage }],
      }])
    }
  }, [introMessage, messages.length, setMessages]);

  // Auto-scroll to bottom
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' })
  }, [messages, status])

  // Focus input on mount (after bubble click opens the widget)
  useEffect(() => {
    const timer = setTimeout(() => {
      textareaRef.current?.focus()
    }, 150)
    return () => clearTimeout(timer)
  }, [])

  // Refocus input when streaming is done
  useEffect(() => {
    if (status === 'ready') {
      textareaRef.current?.focus()
    }
    if (status !== 'streaming') {
      setIsSpeechPending(false)
    }
  }, [status])

  const sendText = useCallback((raw?: string) => {
    const text = (raw ?? input).trim()
    if (!text || status !== 'ready') return false;

    sendMessage({ text })
    setInput('')
    return true
  }, [input, status, sendMessage, setInput])

  const handleSendMessage = () => {
    sendText()
  }

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    handleSendMessage()
  }

  const handleKeyDown = (e: React.KeyboardEvent<HTMLTextAreaElement>) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault()
      handleSendMessage()
    }
  }

  const speechEnabled = speechToText?.enabled !== false

  const handleSpeechInterim = useCallback((text: string) => {
    setInput(text)
  }, [setInput])

  const handleSpeechFinal = useCallback((text: string) => {
    const finalText = text?.trim()
    if (!finalText || isSpeechPending) return
    setInput(finalText)
    const sent = sendText(finalText)
    if (sent) {
      setIsSpeechPending(true)
    }
  }, [sendText, setInput, isSpeechPending])

  useEffect(() => {
    if (!speechEnabled) {
      setIsMicActive(false)
    }
  }, [speechEnabled])

  const renderOutput = useCallback(
    (messageId: string, content: (string | UIWidget)[]) =>
      content.map((entry, index) => {
        if (typeof entry === 'string') {
          return (
            <div key={`${messageId}-text-${index}`} className="chat-widget__markdown">
              <FormattedMarkdown>{entry}</FormattedMarkdown>
            </div>
          );
        }

        if (!('widgetType' in entry)) {
          return null;
        }

        const widgetDef = widgets?.find((w) => w.widgetType === entry.widgetType);
        if (!widgetDef) {
          return (
            <span key={`${messageId}-unknown-${index}`}>
              [Unknown widget: {entry.widgetType}]
            </span>
          );
        }

        return (
          <div key={`${messageId}-widget-${index}`} className="chat-widget__widget">
            {widgetDef.render(entry)}
          </div>
        );
      }),
    [widgets],
  );

  const widget = (
    <div
      className={cn(classNames.container, className)}
      style={themeVariables}
      data-chat-widget=""
    >
      {title && (
        <div className={classNames.header}>
          <h3 className={classNames.headerTitle}>{title}</h3>
        </div>
      )}

      <div className={classNames.messagesContainer}>
        {messages.map((message) => {
          return (
            <div key={message.id} className={classNames.messageWrapper}>
              <div
                className={cn(
                  classNames.messageRow,
                  message.role === 'user'
                    ? classNames.userMessageRow
                    : classNames.assistantMessageRow
                )}
              >
                {message.content.length > 0 && (
                  <div
                    className={cn(
                      classNames.messageBubble,
                      message.role === 'user'
                        ? classNames.userMessage
                        : classNames.assistantMessage
                    )}
                  >
                    {renderOutput(`${message.id}-content`, message.content)}
                  </div>
                )}
              </div>

              {message.role === 'assistant' && message.thoughts && message.thoughts.length > 0 && (
                <div className={classNames.thoughtsContainer}>
                  <details className="chat-widget__details">
                    <summary className={classNames.thoughtsSummary}>
                      <span className={classNames.thoughtsIconContainer}><ChevronRight className={classNames.thoughtsIcon} /></span>
                      <span>Thoughts</span>
                    </summary>
                    <div className={classNames.thoughtsContent}>
                      <pre className={classNames.thoughtsText}>
                        {renderOutput(`${message.id}-thoughts`, message.thoughts)}
                      </pre>
                    </div>
                  </details>
                </div>
              )}
            </div>
          );
        })}
  



        {/* {renderedMessages.map((message, index, array) => {
          const showVoiceButton =
            Boolean(
              voiceConfig &&
              message.role === 'assistant' &&
              message.output
            )
          const shouldAutoplayVoice =
            Boolean(
              showVoiceButton &&
              voiceConfig?.autoplayLatest &&
              index === array.length - 1
            )

          return (
            <div key={message.id} className={classNames.messageWrapper}>
              <div
                className={cn(
                  classNames.messageRow,
                  message.role === 'user'
                    ? classNames.userMessageRow
                    : classNames.assistantMessageRow
                )}
              >
                {message.parts?.length ? (
                  <div
                    className={cn(
                      classNames.messageBubble,
                      message.role === 'user'
                        ? classNames.userMessage
                        : classNames.assistantMessage
                    )}
                  >
                    {message.output}
                  </div>
                ) : null}
              </div>

            {message.role === 'assistant' && message.parts?.some((part: any) => part.type === 'data-thoughts') && (
              <div className={classNames.thoughtsContainer}>
                <details className="chat-widget__details">
                  <summary className={classNames.thoughtsSummary}>
                    <span className={classNames.thoughtsIconContainer}><ChevronRight className={classNames.thoughtsIcon} /></span>
                    <span>Thoughts</span>
                  </summary>
                  <div className={classNames.thoughtsContent}>
                    <pre className={classNames.thoughtsText}>
                      {message.parts
                        ?.filter((part: any) => part.type === 'data-thoughts')
                        .map((part: any) => Array.isArray(part.data) ? part.data.join('\n') : '')
                        .filter(Boolean)
                        .join('\n')}
                    </pre>
                  </div>
                </details>
              </div>
            )}
              {showVoiceButton && voiceConfig && message.output && (
                <div className={classNames.voiceControl}>
              <VoicePlayerButton
                    text={message.output}
                    voiceUrl={voiceConfig.url}
                    headers={voiceConfig.headers}
                    method={voiceConfig.method}
                    payloadBuilder={voiceConfig.buildPayload}
                    autoplay={shouldAutoplayVoice}
                    ready={status === 'ready'}
                    isListening={isMicActive}
                  />
                </div>
              )}
            </div>
          )
        })} */}

        {status === 'streaming' && (
          <div className={classNames.loadingContainer}>
            <div className={classNames.loadingBubble}>
              <div className={classNames.loadingDots}>
                <div className={classNames.loadingDot}></div>
                <div className={classNames.loadingDot} data-delay="1"></div>
                <div className={classNames.loadingDot} data-delay="2"></div>
              </div>
            </div>
          </div>
        )}

        <div ref={messagesEndRef} />
      </div>

      <div className={classNames.inputContainer}>
        <form onSubmit={handleSubmit} className={classNames.inputForm}>
          <textarea
            ref={textareaRef}
            value={input}
            onChange={(e) => setInput(e.target.value)}
            onKeyDown={handleKeyDown}
            placeholder={inputPlaceholder}
            disabled={status !== 'ready'}
            rows={1}
            className={classNames.textarea}
          />
          <div className={classNames.toolbar}>
            {speechEnabled && (
              <SpeechToTextButton
                className={classNames.micButton}
                disabled={status !== 'ready'}
                silenceTimeout={speechToText?.silenceTimeout}
                language={speechToText?.language}
                finalDelay={speechToText?.finalDelay}
                onInterim={handleSpeechInterim}
                onFinal={handleSpeechFinal}
                onError={speechToText?.onError}
                onListeningChange={setIsMicActive}
              />
            )}
          </div>
          
          <button
            type="button"
            onClick={status === 'streaming' ? stop : handleSendMessage}
            disabled={status === 'streaming' ? false : (!input.trim() || status !== 'ready')}
            className={classNames.submitButton}
          >
            {status === 'streaming' ? (
              <StopCircle className={classNames.sendIcon} />
            ) : (
              <Send className={classNames.sendIcon} />
            )}
          </button>
        </form>
      </div>
    </div>
  )

  return widget
}
