import { Mic, MicOff } from 'lucide-react'
import React, {
  useCallback,
  useEffect,
  useRef,
  useState,
} from 'react'
import { cn } from '../utils/cn'

type SpeechRecognitionConstructor = new () => SpeechRecognition

const getSpeechRecognitionCtor = (): SpeechRecognitionConstructor | null => {
  if (typeof window === 'undefined') return null

  const win = window as typeof window & {
    webkitSpeechRecognition?: SpeechRecognitionConstructor
    SpeechRecognition?: SpeechRecognitionConstructor
  }

  return win.SpeechRecognition || win.webkitSpeechRecognition || null
}

const clamp01 = (value: number) => Math.max(0, Math.min(1, value))

const joinSegments = (...parts: string[]) =>
  parts
    .map(part => part.trim())
    .filter(Boolean)
    .join(' ')

export interface SpeechToTextButtonProps {
  onInterim?: (text: string) => void
  onFinal?: (text: string) => void
  onError?: (error: string) => void
  onListeningChange?: (listening: boolean) => void
  silenceTimeout?: number
  finalDelay?: number
  language?: string
  disabled?: boolean
  className?: string
}

export function SpeechToTextButton({
  onInterim,
  onFinal,
  onError,
  onListeningChange,
  silenceTimeout = 3000,
  finalDelay = 1500,
  language,
  disabled,
  className,
}: SpeechToTextButtonProps) {
  const [isListening, setIsListening] = useState(false)
  const [isSupported, setIsSupported] = useState(false)
  const [countdown, setCountdown] = useState(0)
  const recognitionRef = useRef<SpeechRecognition | null>(null)
  const [countdownActive, setCountdownActive] = useState(false)
  const silenceTimer = useRef<ReturnType<typeof setTimeout> | null>(null)
  const finalTimer = useRef<ReturnType<typeof setTimeout> | null>(null)
  const finalTextRef = useRef('')
  const countdownFrameRef = useRef<number | null>(null)
  const countdownEndRef = useRef(0)

  const recognitionLanguage = language ?? 'en-US'

  const clearSilenceTimer = useCallback(() => {
    if (silenceTimer.current) {
      clearTimeout(silenceTimer.current)
      silenceTimer.current = null
    }
  }, [])

  const clearCountdownFrame = useCallback(() => {
    if (countdownFrameRef.current) {
      cancelAnimationFrame(countdownFrameRef.current)
      countdownFrameRef.current = null
    }
  }, [])

  const clearFinalTimer = useCallback(() => {
    if (finalTimer.current) {
      clearTimeout(finalTimer.current)
      finalTimer.current = null
    }
  }, [])

  const stopListening = useCallback(() => {
    clearSilenceTimer()
    clearFinalTimer()
    clearCountdownFrame()
    setCountdown(0)
    setCountdownActive(false)
    countdownEndRef.current = 0
    recognitionRef.current?.stop()
  }, [clearSilenceTimer, clearFinalTimer, clearCountdownFrame])

  const handleRecognitionError = useCallback((message: string) => {
    console.error('Speech recognition error:', message)
    onError?.(message)
  }, [onError])

  const resetSilenceTimer = useCallback(() => {
    if (!silenceTimeout) return
    clearSilenceTimer()
    silenceTimer.current = setTimeout(stopListening, silenceTimeout)
  }, [silenceTimeout, clearSilenceTimer, stopListening])

  const dispatchInterim = useCallback((text: string) => {
    if (text) {
      onInterim?.(text)
    }
  }, [onInterim])

  const dispatchFinal = useCallback((text: string) => {
    if (text) {
      onFinal?.(text)
    }
  }, [onFinal])

  const flushFinalText = useCallback(() => {
    const buffered = finalTextRef.current.trim()
    if (buffered) {
      dispatchFinal(buffered)
    }
    finalTextRef.current = ''
    clearFinalTimer()
  }, [dispatchFinal, clearFinalTimer])

  const updateCountdown = useCallback(() => {
    if (!countdownEndRef.current || !finalDelay) {
      setCountdown(0)
      setCountdownActive(false)
      countdownFrameRef.current = null
      return
    }

    const remaining = countdownEndRef.current - Date.now()
    const normalizedCountdown = clamp01(remaining / finalDelay)

    setCountdown(normalizedCountdown)

    if (remaining <= 0) {
      setCountdownActive(false)
      countdownFrameRef.current = null
      return
    }

    countdownFrameRef.current = requestAnimationFrame(updateCountdown)
  }, [finalDelay])

  const startCountdown = useCallback(() => {
    if (!finalDelay) {
      setCountdown(0)
      setCountdownActive(false)
      return
    }

    countdownEndRef.current = Date.now() + finalDelay
    setCountdown(1)
    setCountdownActive(true)
    clearCountdownFrame()
    countdownFrameRef.current = requestAnimationFrame(updateCountdown)
  }, [finalDelay, clearCountdownFrame, updateCountdown])

  const scheduleFinalFlush = useCallback(() => {
    clearFinalTimer()

    if (!finalDelay) {
      flushFinalText()
      return
    }

    startCountdown()
    finalTimer.current = setTimeout(flushFinalText, finalDelay)
  }, [clearFinalTimer, finalDelay, flushFinalText, startCountdown])

  const attachRecognitionHandlers = useCallback((recognition: SpeechRecognition) => {
    recognition.continuous = true
    recognition.interimResults = true
    recognition.lang = recognitionLanguage

    recognition.onresult = (event: SpeechRecognitionEvent) => {
      let interimText = ''
      let finalText = ''

      for (let i = event.resultIndex; i < event.results.length; i++) {
        const result = event.results[i]
        if (!result) continue
        const transcript = result[0]?.transcript ?? ''
        if (result.isFinal) {
          finalText += transcript
        } else {
          interimText += transcript
        }
      }

      const interimOnly = interimText.trim()
      if (interimOnly) {
        startCountdown()
        const combined = joinSegments(finalTextRef.current, interimOnly)
        dispatchInterim(combined)
      } else {
        const persisted = finalTextRef.current.trim()
        if (persisted) {
          dispatchInterim(persisted)
        }
      }

      const resolvedFinal = finalText.trim()
      if (resolvedFinal) {
        finalTextRef.current = joinSegments(finalTextRef.current, resolvedFinal)
        dispatchInterim(finalTextRef.current)
        scheduleFinalFlush()
        recognition.stop()
      }

      resetSilenceTimer()
    }

    recognition.onerror = (event: SpeechRecognitionErrorEvent) => {
      if (event.error === 'no-speech') {
        resetSilenceTimer()
        return
      }
      handleRecognitionError(event.error)
    }

    recognition.onstart = () => {
      setIsListening(true)
      onListeningChange?.(true)
      resetSilenceTimer()
    }

    recognition.onend = () => {
      setIsListening(false)
      onListeningChange?.(false)
      clearSilenceTimer()
      if (!finalTimer.current) {
        flushFinalText()
      }
    }
  }, [recognitionLanguage, resetSilenceTimer, startCountdown, dispatchInterim, scheduleFinalFlush, handleRecognitionError, onListeningChange, clearSilenceTimer, flushFinalText, countdownActive])

  const ensureRecognition = useCallback((): SpeechRecognition | null => {
    if (typeof window === 'undefined') {
      return null
    }

    const Ctor = getSpeechRecognitionCtor()
    if (!Ctor) {
      handleRecognitionError('Speech recognition is not supported in this browser.')
      return null
    }

    if (!recognitionRef.current) {
      recognitionRef.current = new Ctor()
    }

    attachRecognitionHandlers(recognitionRef.current)
    return recognitionRef.current
  }, [attachRecognitionHandlers, handleRecognitionError])

  const toggleListening = useCallback(() => {
    if (disabled) {
      return
    }

    if (isListening) {
      stopListening()
      return
    }

    const recognition = ensureRecognition()
    if (!recognition) {
      return
    }

    try {
      recognition.start()
    } catch (error) {
      handleRecognitionError('Microphone permission denied or unavailable.')
    }
  }, [disabled, isListening, stopListening, ensureRecognition, handleRecognitionError])

  useEffect(() => {
    if (disabled && isListening) {
      stopListening()
    }
  }, [disabled, isListening, stopListening])

  useEffect(() => {
    setIsSupported(typeof window !== 'undefined' && !!getSpeechRecognitionCtor())
  }, [])

  useEffect(() => {
    return () => {
      clearSilenceTimer()
      clearFinalTimer()
      clearCountdownFrame()
      recognitionRef.current?.stop()
      recognitionRef.current = null
    }
  }, [clearSilenceTimer, clearFinalTimer, clearCountdownFrame])

  if (!isSupported) {
    return null
  }

  return (
    <button
      type="button"
      className={cn(
        'chat-widget__mic-button',
        isListening && 'chat-widget__mic-button--active',
        className
      )}
      onClick={toggleListening}
      disabled={disabled}
      aria-pressed={isListening}
      aria-label="Toggle microphone"
    >
      {isListening ? (
        <Mic className="chat-widget__mic-icon" />
      ) : (
        <MicOff className="chat-widget__mic-icon" />
      )}
      {finalDelay > 0 && isListening && countdownActive && (
        <span className="chat-widget__mic-bar" aria-hidden="true">
          <span
            className="chat-widget__mic-bar-fill"
            style={{ transform: `scaleX(${clamp01(countdown)})` }}
          />
        </span>
      )}
    </button>
  )
}
