import './PopupChatWidget.css'

import { MessageSquare, X } from 'lucide-react'
import React, { useEffect, useMemo, useState } from 'react'

import { cn } from '../utils/cn'
import { ChatWidget, ChatWidgetConfig } from './ChatWidget'

export interface PopupAnchorConfig {
  enabled?: boolean
  label?: string
  position?: 'bottom-end' | 'bottom-start'
  initiallyOpen?: boolean
  render?: (options: { isOpen: boolean; toggle: () => void }) => React.ReactNode
  closeButton?: PopupCloseConfig
}

export interface PopupCloseConfig {
  render?: (options: { toggle: () => void }) => React.ReactNode
  position?: 'top-end' | 'top-start'
  show?: boolean
}

export interface PopupChatWidgetProps extends ChatWidgetConfig {
  anchor?: PopupAnchorConfig
  closeButton?: PopupCloseConfig
  popupSize?: {
    width?: string
    height?: string
    maxWidth?: string
    maxHeight?: string
  }
  /** When provided, overrides the open/closed state */
  openOverride?: boolean
  /** Callback when open state should change (used when openOverride is controlled) */
  onToggleOpen?: (nextOpen: boolean) => void
}

export function PopupChatWidget({ anchor, ...props }: PopupChatWidgetProps) {
  const [isOpen, setIsOpen] = useState(anchor?.initiallyOpen ?? false)
  const [hasOpened, setHasOpened] = useState(anchor?.initiallyOpen ?? false)
  const openResolved = props.openOverride ?? isOpen

  useEffect(() => {
    if (props.openOverride === undefined) return
    setIsOpen(props.openOverride)
    if (props.openOverride) {
      setHasOpened(true)
    }
  }, [props.openOverride])

  const handleToggle = () => {
    const next = !openResolved
    if (next) setHasOpened(true)
    if (props.onToggleOpen) {
      props.onToggleOpen(next)
    } else {
      setIsOpen(next)
    }
  }

  const anchorPositionClass =
    anchor?.position === 'bottom-start' ? 'chat-popup--start' : 'chat-popup--end'
  const closeConfig = anchor?.closeButton ?? props.closeButton
  const showClose = closeConfig?.show !== false

  const themeStyle = useMemo(() => {
    const primary = props.styles?.primaryColor ?? '#3b82f6'
    const primaryText = props.styles?.primaryTextColor ?? '#ffffff'
    const background = props.styles?.backgroundColor ?? '#ffffff'
    const border = props.styles?.borderColor ?? '#e5e7eb'
    const headerText = props.styles?.headerTextColor ?? '#ffffff'

    const mixWithBlack = (hex: string, ratio = 0.15) => {
      const normalized = hex.replace('#', '')
      if (!/^[0-9a-fA-F]{6}$/.test(normalized)) return hex
      const r = parseInt(normalized.slice(0, 2), 16)
      const g = parseInt(normalized.slice(2, 4), 16)
      const b = parseInt(normalized.slice(4, 6), 16)
      const mix = (value: number) => Math.round(value * (1 - ratio))
      return `#${mix(r).toString(16).padStart(2, '0')}${mix(g)
        .toString(16)
        .padStart(2, '0')}${mix(b).toString(16).padStart(2, '0')}`
    }

    return {
      '--chat-primary': primary,
      '--chat-primary-hover': mixWithBlack(primary, 0.2),
      '--chat-primary-text': primaryText,
      '--chat-background': background,
      '--chat-border': border,
      '--chat-header-text': headerText,
    } as React.CSSProperties
  }, [props.styles])

  const popupStyle = useMemo(() => {
    const size = props.popupSize ?? {}
    return {
      width: size.width ?? '600px',
      height: size.height ?? '500px',
      maxWidth: size.maxWidth ?? 'calc(100vw - 40px)',
      maxHeight: size.maxHeight ?? 'calc(100vh - 140px)',
    } as React.CSSProperties
  }, [props.popupSize])

  return (
    <div style={themeStyle}>
      <div className={cn('chat-popup__anchor', anchorPositionClass)}>
        {anchor?.render ? (
          anchor.render({ isOpen: openResolved, toggle: handleToggle })
        ) : (
          !openResolved && (
            <button
              type="button"
              className="chat-popup__anchor-button"
              onClick={handleToggle}
              aria-expanded={isOpen}
              aria-label={anchor?.label ?? 'Open chat'}
            >
              <MessageSquare className="chat-popup__anchor-icon" />
              <span>{anchor?.label ?? 'Chat'}</span>
            </button>
          )
        )}
      </div>

      <div
        className={cn(
          'chat-popup__panel',
          anchorPositionClass,
          openResolved ? 'chat-popup__panel--open' : 'chat-popup__panel--closed'
        )}
      >
        <div className="chat-popup__panel-inner" style={popupStyle}>
          {showClose && (
            <div
              className={cn(
                'chat-popup__close',
                closeConfig?.position === 'top-start'
                  ? 'chat-popup__close--start'
                  : 'chat-popup__close--end',
              )}
            >
              {closeConfig?.render ? (
                closeConfig.render({ toggle: handleToggle })
              ) : (
                <button
                  type="button"
                  className="chat-popup__close-button"
                  onClick={handleToggle}
                  aria-label="Close chat"
                >
                  <X className="chat-popup__close-icon" />
                </button>
              )}
            </div>
          )}
          {hasOpened && <ChatWidget {...props} />}
        </div>
      </div>
    </div>
  )
}
