import './components/ChatWidget.css';

import { createRoot } from 'react-dom/client';

import { ChatWidget } from './components/ChatWidget';
import { PopupChatWidget, PopupChatWidgetProps } from './components/PopupChatWidget';

/**
 * Embeddable Chat Widget
 *
 * This file creates a standalone chat widget that can be embedded in any website
 * without requiring React in the host application. Uses Shadow DOM for style isolation.
 */

export interface EmbeddableChatWidgetConfig extends PopupChatWidgetProps {
  containerId?: string;
}

class EmbeddableChatWidget {
  container: HTMLDivElement | null = null;
  root: ReturnType<typeof createRoot> | null = null;
  config: EmbeddableChatWidgetConfig;
  openOverride?: boolean;

  constructor(config: EmbeddableChatWidgetConfig) {
    this.config = config;
  }

  async init() {
    // Only destroy if we're switching to a different container
    if (this.container && this.config.containerId &&
        this.container.id !== this.config.containerId) {
      this.destroy();
    }

    // If already initialized with same container, nothing else to do here
    if (this.root && this.container) {
      return;
    }

    // Create or use existing container
    if (this.config.containerId) {
      this.container = document.getElementById(this.config.containerId) as HTMLDivElement;
      if (!this.container) {
        console.error(`Container with id "${this.config.containerId}" not found`);
        return;
      }
      
      // Don't apply positioning styles when using provided container
    } else if (this.config.anchor?.enabled) {
      // For anchor/popup mode, let ChatWidget handle positioning
      this.container = document.createElement('div');
      document.body.appendChild(this.container);
    } else {
      // Only create auto-positioned container if no containerId provided
      this.container = document.createElement('div');
      this.container.id = 'chat-widget-container';
      this.container.style.cssText = `
        position: fixed;
        bottom: 20px;
        right: 20px;
        width: 384px;
        height: 500px;
        z-index: 999999;
        box-shadow: 0 20px 45px rgba(15, 23, 42, 0.12);
        border-radius: 12px;
        overflow: hidden;
      `;
      document.body.appendChild(this.container);
    }

    // Keep hidden until explicitly shown
    if (this.container) {
      this.container.style.display = 'block';
    }

    // Render React component directly (styles are included via CSS import)
    this.root = createRoot(this.container);

    const { containerId, ...chatWidgetProps } = this.config;
    // containerId is used for container selection, not passed to ChatWidget
    const Component = chatWidgetProps.anchor?.enabled ? PopupChatWidget : ChatWidget;
    this.root.render(
      <Component
        {...chatWidgetProps}
        openOverride={this.openOverride}
        onToggleOpen={(next) => {
          this.openOverride = next;
          // keep anchor visible; rerender to apply state
          this.updateConfig({});
        }}
      />
    );
  }

  destroy() {
    // Cleanup React root
    if (this.root) {
      this.root.unmount();
      this.root = null;
    }

    // Remove auto-created container (but not user-provided ones)
    if (this.container && this.container.parentNode && !this.config.containerId) {
      this.container.parentNode.removeChild(this.container);
    }

    // Clear container contents if it was user-provided
    if (this.container && this.config.containerId) {
      this.container.innerHTML = '';
    }

    this.container = null;
  }

  // Method to update configuration
  updateConfig(newConfig: Partial<EmbeddableChatWidgetConfig>) {
    const mergedApi = newConfig.api
      ? { ...this.config.api, ...newConfig.api }
      : this.config.api;
    this.config = { ...this.config, ...newConfig, api: mergedApi };
    if (this.root) {
      const { containerId, ...chatWidgetProps } = this.config;
      // containerId is used for container selection, not passed to ChatWidget
      const Component = chatWidgetProps.anchor?.enabled ? PopupChatWidget : ChatWidget;
      this.root.render(
        <Component
          {...chatWidgetProps}
          openOverride={this.openOverride}
          onToggleOpen={(next) => {
            this.openOverride = next;
            this.updateConfig({});
          }}
        />
      );
    }
  }

  updateToken(token: string) {
    this.updateConfig({
      api: {
        ...(this.config.api || { baseUrl: '', token: '' }),
        token,
      },
    });
  }
}

// Global initialization function
declare global {
  interface Window {
    ChatWidget: {
      init: (config: EmbeddableChatWidgetConfig) => Promise<void>;
      destroy: () => void;
      hide: () => void;
      show: () => void;
      updateConfig: (config: Partial<EmbeddableChatWidgetConfig>) => void;
      updateToken: (token: string) => void;
    };
  }
}

let widgetInstance: EmbeddableChatWidget | null = null;

// Auto-initialize on script load
window.ChatWidget = {
  init: async (config: EmbeddableChatWidgetConfig) => {
    if (!widgetInstance) {
      widgetInstance = new EmbeddableChatWidget(config);
    } else {
      // Update config for existing instance
      widgetInstance.updateConfig(config);
    }
    await widgetInstance.init();
  },
  destroy: () => {
    if (widgetInstance) {
      widgetInstance.destroy();
      widgetInstance = null;
    }
  },
  hide: () => {
    if (widgetInstance) {
      widgetInstance.openOverride = false;
      if (widgetInstance.container) {
        widgetInstance.container.style.display = 'block';
      }
      widgetInstance.updateConfig({});
    }
  },
  show: () => {
    if (widgetInstance) {
      widgetInstance.openOverride = true;
      if (widgetInstance.container) {
        widgetInstance.container.style.display = 'block';
      }

      // If container is empty but we have a root, re-render
      if (widgetInstance.container && widgetInstance.container.children.length === 0 && widgetInstance.root) {
        const { containerId, ...chatWidgetProps } = widgetInstance.config;
        const Component = chatWidgetProps.anchor?.enabled ? PopupChatWidget : ChatWidget;
        widgetInstance.root.render(
          <Component
            {...chatWidgetProps}
            openOverride={widgetInstance.openOverride}
            onToggleOpen={(next) => {
              widgetInstance!.openOverride = next;
              widgetInstance!.updateConfig({});
            }}
          />
        );
      }
      widgetInstance.updateConfig({});
    }
  },
  updateConfig: (config: Partial<EmbeddableChatWidgetConfig>) => {
    if (widgetInstance) {
      widgetInstance.updateConfig(config);
    }
  },
  updateToken: (token: string) => {
    if (widgetInstance) {
      widgetInstance.updateToken(token);
    }
  },
};

// Auto-initialize if config is found in window or data attributes
if (typeof window !== 'undefined') {
  // Check for global config
  const globalConfig = (window as unknown as Record<string, unknown>).chatWidgetConfig;
  if (globalConfig) {
    window.ChatWidget.init(globalConfig as EmbeddableChatWidgetConfig).catch(console.error);
  }

  // Check for data attributes on script tag
  const currentScript = document.currentScript as HTMLScriptElement | null;
  if (currentScript && currentScript.hasAttribute('data-config')) {
    try {
      const config = JSON.parse(currentScript.getAttribute('data-config') || '{}');
      window.ChatWidget.init(config).catch(console.error);
    } catch (error) {
      console.error('Failed to parse chat widget config from data-config attribute:', error);
    }
  }

  // Auto-detect from multiple data attributes
  if (
    currentScript &&
    currentScript.hasAttribute('data-api-base-url') &&
    currentScript.hasAttribute('data-token') &&
    currentScript.hasAttribute('data-thread-id')
  ) {
    const config: EmbeddableChatWidgetConfig = {
      api: {
        baseUrl: currentScript.getAttribute('data-api-base-url') || '',
        token: currentScript.getAttribute('data-token') || '',
      },
      threadId: currentScript.getAttribute('data-thread-id') || '',
      containerId: currentScript.getAttribute('data-container-id') || undefined,
      title: currentScript.getAttribute('data-title') || undefined,
      introMessage: currentScript.getAttribute('data-intro-message') || undefined,
    };
    const agentId = currentScript.getAttribute('data-agent-id') || undefined;
    const agentExecutionId = currentScript.getAttribute('data-agent-execution-id') || undefined;
    if (agentId) {
      config.agentId = agentId;
    }
    if (agentExecutionId) {
      config.agentExecutionId = agentExecutionId;
    }

    if (currentScript.getAttribute('data-anchor') === 'true') {
      config.anchor = { enabled: true };
    }

    // Parse headers if provided
    if (currentScript.hasAttribute('data-headers')) {
      try {
        const parsedHeaders = JSON.parse(currentScript.getAttribute('data-headers') || '{}');
        config.api.headers = parsedHeaders;
      } catch (error) {
        console.error('Failed to parse headers from data-headers attribute:', error);
      }
    }

    window.ChatWidget.init(config).catch(console.error);
  }
}
