import { useChat as useAIChat } from '@ai-sdk/react'
import { DefaultChatTransport, UIMessage } from 'ai'
import { useEffect, useState } from 'react'

import { UIWidget } from '../schemas'

export interface ApiConfig {
  /** The base URL where /chat and /chat/messages are hosted */
  baseUrl: string;
  /** JWT token generated from Secret */
  token: string;
  headers?: Record<string, string>;
}

export interface UseChatConfig {
  /** The server API configuration */
  api: ApiConfig;
  /** Thread ID for keeping conversation history */
  threadId: string;
  /** The Agent ID that this chat connects to. Either agentId or agentExecutionId must be provided */
  agentId?: string;
  /** The Agent Orchestration ID that this chat connects to. Either agentId or agentExecutionId must be provided */
  agentExecutionId?: string;
  /** additional context (anything) that needs to be passed to the LLM */
  dataContext?: unknown;

  onError?: (error: Error) => void;
  /** Called when API returns 401/unauthorized (e.g., token expiry) */
  onAuthError?: () => void;
  onFinish?: (message: any) => void;
  onData?: (data: any) => void;
  onMessage?: (message: UIMessage) => void;
}

export interface ChatMessage {
  id: string;
  role: 'user' | 'assistant';
  content: (string | UIWidget)[];
  thoughts?: (string | UIWidget)[];
  createdAt?: Date;
}

export function useChat({
  api,
  threadId,
  agentId,
  agentExecutionId,
  dataContext,
  onError,
  onAuthError,
  onFinish,
  onData,
  onMessage,
}: UseChatConfig) {
  const { baseUrl, token, headers: customHeaders = {} } = api;

  const {
    messages,
    status,
    sendMessage,
    stop,
    setMessages,
  } = useAIChat({
    id: threadId,
    transport: new DefaultChatTransport({
      api: `${baseUrl}/chat`,
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Bearer ${token}`,
        'thread-id': threadId,
        ...(agentId ? { 'agent-id': agentId } : {}),
        ...(agentExecutionId ? { 'agent-execution-id': agentExecutionId } : {}),
        ...customHeaders,
      },
      // only send last message to server
      prepareSendMessagesRequest({ messages, id }) {
        return {
          body: {
            id,
            message: messages[messages.length - 1],
            dataContext,
          }
        };
      },
    }),
    onError: (error) => {
      console.error('Chat error:', error)
      onError?.(error)
    },
    onFinish: (options) => {
      onFinish?.(options);
    },
    onData: (dataPart) => {
      onData?.(dataPart)
    }
  })

  const [isLoadingInitial, setIsLoadingInitial] = useState(false)
  useEffect(() => {
    const fetchInitialMessages = async () => {

      setIsLoadingInitial(true);
      try {
        const response = await fetch(`${baseUrl}/chat/messages`, {
          method: 'GET',
          headers: {
            Authorization: `Bearer ${token}`,
            'thread-id': threadId,
          },
        })

        if (!response.ok) {
          if (response.status === 401) {
            onAuthError?.();
          }
          throw new Error(`Failed to fetch messages: ${response.statusText}`)
        }

        const data = await response.json()
        const initialMessages = data.messages || []

        if (initialMessages.length > 0) {
          setMessages(initialMessages);
        }
      } catch (error) {
        console.error('Error fetching initial messages:', error)
        onError?.(error as Error)
      } finally {
        setIsLoadingInitial(false);
      }
    }

    fetchInitialMessages();
  }, [agentExecutionId, agentId, baseUrl, threadId, token])

  useEffect(() => {
    if (!onMessage || messages.length === 0) return;
    const lastMessage = messages[messages.length - 1];
    if (lastMessage) {
      onMessage(lastMessage);
    }
  }, [messages, onMessage]);

  // format the messages
  const rtnMessages = messages.map((msg, index, array): ChatMessage => {
    const content: (string | UIWidget)[] = [];
    const thoughts: (string | UIWidget)[] = [];

    msg.parts.forEach(part => {
      if (part.type === 'text') {
        content.push(part.text);
      } else if (part.type === 'data-ui') {
        content.push(...part.data as UIWidget[]);
      } else if (part.type === 'data-thoughts') {
        thoughts.push(...part.data as (string | UIWidget)[]);
      }
    });

    // if the last part is marked as data-thoughts, append to the main content.
    // However we don't want to do it for the last message while streaming (not done yet)
    const isLastMessage = index === array.length - 1;
    if (msg.role === 'assistant' && (!isLastMessage || status !== 'streaming')) {
      const lastPart = msg.parts.at(-1);
      if (lastPart && lastPart.type === 'data-thoughts') {
        content.push(...lastPart.data as (string | UIWidget)[]);
      }
    }

    return {
      id: msg.id,
      role: msg.role as 'user' | 'assistant',
      content,
      thoughts,
    };
  });

  return {
    messages: rtnMessages,
    status,
    isLoadingInitial,
    sendMessage,
    stop,
    setMessages,
  }
}
